<template>
  <div class="map">
    <div class="heders"> 
      <b>全国(含港澳台) <code>{{times}}数据统计</code></b>
      <ul>
        <li>
          <p>现存确诊</p>
          <p>{{econNum}}</p>
          <p>较昨日<code> {{ addecon_new}}</code></p>
        </li>
        <li>
          <p>现存确诊重症</p>
          <p>{{heconNum}}</p>
          <p>较昨日 <code> {{ addhecon_new}}</code></p>
        </li>
        <li>
          <p>现存疑似</p>
          <p>{{sustotal}}</p>
          <p>较昨日 <code>{{ wjw_addsus_new}}</code></p>
        </li>
        <li>
          <p>累计确诊</p>
          <p>{{ALLtoal}}</p>
          <p>较昨日 <code>{{ addcon_new}}</code></p>
        </li>
        <li>
          <p>累计死亡</p>
          <p>{{deathtotal}}</p>
          <p>较昨日 <code>{{ adddeath_new}}</code></p>
        </li>
        <li>
          <p>累计治愈</p>
          <p>{{curetotal}}</p>
          <p>较昨日 <code>{{ addcure_new}}</code></p>
        </li>
      </ul>
    </div>
    <div id="chart" style="width: 100%;height:800px;">
    </div>
  </div>
</template>

<script>
import echarts from "echarts";
import "echarts/map/js/china";
import jsonp from "jsonp";
import { setInterval } from 'timers';

let option = {
  title: {
    text: "2020年新型冠状病毒肺炎疫情地图",
    x: "center",
    y:'top',
    textAlign:'left',
    textStyle: {
      color: "#9c0505"
    }
  },
  tooltip: {
    trigger: "item",
    formatter: "地区：{b}<br/>现有确诊：{c}<br/> ", 
  },
  series: [
    {
      name: "six",
      type: "map",
      mapType: "china",
      label: {
        show: true,
        color: "black"
      },
      emphasis: {
        label: {
          // color: "#fff",
          fontSize: 15
        },
        itemStyle: {
          // areaColor: "green"
        }
      }
    }
  ],
  visualMap: {
    type: "piecewise",
    show: true,
    pieces: [
      { min: 50000 }, // 不指定 max，表示 max 为无限大（Infinity）。
      { min: 10000, max: 50000 },
      { min: 5000, max: 9999 },
      { min: 1000, max: 4999 },
      { min: 200, max: 999 },
      { min: 1, max: 199, label: "10 到 200（自定义label）" },
      { value: 0 } // 表示 value 等于 123 的情况。
    ],
    inRange: {
      color: ["#fff", "#ffaa85", "#660208"],
      symbolSize: [30, 100]
    }
  }
};
export default {
  name: "Map_home",
  data() {
    return {
      myChart:'',
      times:"",
      econNum: "",
      addecon_new:"",

      heconNum:"",
      addhecon_new:"",

      sustotal:"",
      wjw_addsus_new:"",

      ALLtoal:"",
      addcon_new:"",

      deathtotal:"",
      adddeath_new:"",

      curetotal:"",
      addcure_new:"",
    };
  },
  mounted() {
    this.getdata();
    // setInterval(() => {
    // }, 10000);
    this.myChart = echarts.init(document.getElementById("chart"));
  },
  methods: {
    getdata() {
      jsonp(
        "https://interface.sina.cn/news/wap/fymap2020_data.d.json",
        (err, res) => {
          let list = res.data.list.map(item => {
            return { name: item.name, value: item.value,econNum:item.econNum,cureNum:item.cureNum,deathNum:item.deathNum };
          });
          console.log("数据：",res.data,res.data.gntotal);
          this.times = res.data.times;
          this.econNum = res.data.econNum; //
          this.addecon_new = res.data.add_daily.addecon_new;//

          this.heconNum = res.data.heconNum; //
          this.addhecon_new =res.data.add_daily.addhecon_new;//

          this.sustotal = res.data.sustotal; //全国疑似
          this.wjw_addsus_new = res.data.add_daily.wjw_addsus_new;//

          this.ALLtoal = res.data.gntotal;//全国累计确诊
          this.addcon_new = res.data.add_daily.addcon_new;//

          this.deathtotal = res.data.deathtotal; //全国累计死亡
          this.adddeath_new = res.data.add_daily.adddeath_new;

          this.curetotal = res.data.curetotal; //全国治愈
          this.addcure_new = res.data.add_daily.addcure_new;//
          option.series[0].data=list
          // console.log("list：",list);
          this.myChart.setOption(option)

        }
      );
    }
  },
  beforeDestroy () {
    //销毁缓存
  }

};
</script>

